.our-team {
    padding: 120px 0 55px;

    @media screen and (max-width: 768px) {
        background-color: #fff;
        padding: 30px 0 33px;
    }

    &__custom-container {
        max-width: 1078px;
        margin: 0 auto;

        @media screen and (max-width: 1200px) {
            width: 1000px;
        }

        @media screen and (max-width: 992px) {
            width: 700px;
        }

        @media screen and (max-width: 768px) {
            width: 670px;
            padding: 0 24px;
        }

        @media screen and (max-width: 670px) {
            width: 100%;
        }
    }

    &__title {
        color: #151836;
        text-align: center;
        font-feature-settings: 'clig' off, 'liga' off;
        font-family: Turbine, sans-serif;
        font-size: 48px;
        font-style: normal;
        font-weight: 700;
        line-height: 1.2;
        margin-bottom: 0;

        @media screen and (max-width: 1200px) {
            font-size: 36px;
        }

        @media screen and (max-width: 1024px) {
            font-size: 28px;
        }

        @media screen and (max-width: 768px) {
            font-size: 24px;
            margin-bottom: 8px;
        }
    }

    &__subtitle {
        color: #151836;
        text-align: center;
        font-feature-settings: 'clig' off, 'liga' off;
        font-family: Turbine, sans-serif;
        font-size: 48px;
        font-style: normal;
        font-weight: 300;
        line-height: 1.2;
        margin-bottom: 24px;

        @media screen and (max-width: 1200px) {
            font-size: 36px;
        }

        @media screen and (max-width: 1024px) {
            font-size: 28px;
        }

        @media screen and (max-width: 768px) {
            font-size: 24px;
            margin-bottom: 16px;
        }
    }

    &__description {
        color: #17181a;
        text-align: center;
        font-feature-settings: 'clig' off, 'liga' off;
        font-family: Turbine, sans-serif;
        font-size: 30px;
        font-style: normal;
        font-weight: 300;
        line-height: 1.2;
        margin-bottom: 64px;

        @media screen and (max-width: 1200px) {
            font-size: 26px;
        }

        @media screen and (max-width: 1024px) {
            font-size: 22px;
        }

        @media screen and (max-width: 768px) {
            font-size: 18px;
            margin-bottom: 40px;
        }
    }

    &__container {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        gap: 42px;

        @media screen and (max-width: 991px) {
            grid-template-columns: 1fr 1fr;
        }

        @media screen and (max-width: 768px) {
            gap: 26px;
        }

        @media screen and (max-width: 670px) {
            grid-template-columns: 155px 155px;
            gap: 16px;
            justify-content: center;
        }
    }

    &__member {
        text-align: center;

        &.opened {
            background-color: #151836;
            border-radius: 200px 200px 0 0;

            .our-team__member-description {
                max-height: 1500px;
                margin-top: 16px;
                padding: 0 24px 80px;

                @media screen and (max-width: 570px) {
                    padding: 0 16px 8px;
                    font-size: 10px;
                    margin-top: 8px;
                }
            }

            .our-team__member-name,
            .our-team__member-position,
            .our-team__member-description {
                color: #fff;
            }
        }
    }

    &__member-photo {
        margin: 0 0 16px;

        @media screen and (max-width: 570px) {
            margin: 0 0 8px;
        }

        img {
            object-fit: cover;
            max-width: 100%;
            height: 100%;
            width: 100%;
        }
    }

    &__member-name {
        color: #17181a;
        text-align: center;
        font-size: 20px;
        font-style: normal;
        font-weight: 700;

        @media screen and (max-width: 1024px) {
            font-size: 18px;
        }

        @media screen and (max-width: 768px) {
            font-size: 16px;
        }

        @media screen and (max-width: 470px) {
            font-size: 12px;
        }
    }
    &__member-position {
        font-size: 18px;
        font-style: normal;
        font-weight: 500;

        @media screen and (max-width: 1024px) {
            font-size: 18px;
        }

        @media screen and (max-width: 768px) {
            font-size: 16px;
        }

        @media screen and (max-width: 470px) {
            font-size: 12px;
        }
    }
    &__member-description {
        max-height: 0;
        height: auto;
        overflow: hidden;
        font-weight: 400;
        transition: max-height 0.3s;
    }

    .element-to-be-clipped {
        clip-path: url(#svg-clip-desktop);

        @media screen and (max-width: 1200px) {
            clip-path: url(#svg-clip-tablet);
        }

        @media screen and (max-width: 1024px) {
            clip-path: url(#svg-clip-desktop);
        }

        @media screen and (max-width: 768px) {
            clip-path: url(#svg-clip-tablet);
        }

        @media screen and (max-width: 670px) {
            clip-path: url(#svg-clip-mob);
        }
    }
}
